import React, { useEffect, useState, useRef } from 'react';
import axios from 'axios';
import Chart from 'chart.js/auto';

function Graficos() {
    const [datosGrafico, setDatosGrafico] = useState(null);
    const chartRef = useRef(null);

    useEffect(() => {
        axios.get('http://localhost:3001/progresoempates')
            .then(response => {
                setDatosGrafico(response.data);
            })
            .catch(error => console.error('Error fetching data:', error));
    }, []);

    function formatDate(date) {
        const options = { day: '2-digit', month: 'long' };
        const formattedDate = new Date(date).toLocaleDateString('es-ES', options);
        const capitalizedDate = formattedDate.replace(/\b\w/g, (char) => char.toUpperCase());
        return capitalizedDate.replace(/ De /g, ' ');
    }

    useEffect(() => {
        if (datosGrafico) {
            const labels = datosGrafico.map(item => formatDate(item.fecha));
            const data = datosGrafico.map(item => item.acumulado_progresivo);
            const trendColor = [];

            for (let i = 0; i < data.length; i++) {
                if (i > 0) {
                    const difference = data[i] - data[i - 1];
                    trendColor.push(difference > 0 ? '#0FC2C0' : '#FF5757');
                } else {
                    trendColor.push('rgba(20, 10, 10, 50)');
                }
            }

            if (chartRef.current) {
                chartRef.current.destroy();
            }

            const ctx = document.getElementById('grafico-cascada');

            chartRef.current = new Chart(ctx, {
                type: 'line',
                data: {
                    labels: labels,
                    datasets: [{
                        data: data,
                        borderColor: '#4bc0c0',
                        borderWidth: 3,
                        pointRadius: 4,
                        pointBackgroundColor: trendColor,
                        pointBorderColor: '#DEEFE7',
                        pointHoverRadius: 6,
                        pointHoverBackgroundColor: trendColor,
                        pointHoverBorderColor: '#000000',
                    }]
                },
                options: {
                    responsive: true,
                    maintainAspectRatio: false,
                    scales: {
                        x: {
                            title: {
                                display: true,
                                color: 'black',
                                font: {
                                    family: 'andika',
                                    size: 20,
                                },
                                padding: {
                                    top: 20,
                                    bottom: -45,
                                },
                            },
                            ticks: {
                                color: '#000000',
                                font: {
                                    family: 'Montserrat',
                                    size: 12,
                                    weight: 'bold'
                                },
                                maxRotation: 90,
                                minRotation: 90,
                                autoSkip: false,
                                callback: function(value, index) {
                                    // Mostrar solo cada cuarta etiqueta
                                    return index % 4 === 0 ? this.getLabelForValue(value) : '';
                                }
                            },
                            grid: {
                                display: false
                            }
                        },
                        y: {
                            position: 'right',
                            ticks: {
                                color: '#000000',
                                font: {
                                    family: 'Montserrat',
                                    size: 12,
                                    weight: 'bold'
                                },
                                stepSize: 10,
                                callback: function (value) {
                                    return 'S/. ' + value;
                                }
                            },
                            grid: {
                                color: 'rgba(200, 200, 200, 0.2)'
                            }
                        }
                    },
                    plugins: {
                        legend: {
                            display: false // Aquí desactivas la leyenda
                        },
                        tooltip: {
                            enabled: true,
                            callbacks: {
                                label: function (context) {
                                    let label = context.dataset.label || '';
                                    if (label) {
                                        label += ': ';
                                    }
                                    label += 'S/. ' + context.raw;
                                    return label;
                                }
                            }
                        }
                    },
                    animation: {
                        duration: 1000,
                        easing: 'easeInOutBounce'
                    }
                }
            });
        }
    }, [datosGrafico]);

    return (
        <div className='graficorendimiento' style={{ overflowX: 'auto', overflowY: 'hidden' }}>
            <div className='graficos' id='graficos' style={{ width: '100%', overflowX: 'auto', overflowY: 'hidden' }}>
                <canvas id='grafico-cascada'></canvas>
            </div>
        </div>
    );
}

export default Graficos;
