import React from 'react';
import Portada from '../componentes/portada';
import Footer from '../componentes/footer';
import Cajamasdetres from '../componentes/cajamasdetres';
function Masdetres() {
    // Obtener la fecha actual

    // Renderizar solo los pronósticos de la fecha actual
    return (
        <div className='masdetres' id='pronosticosdeldia'>
            <Portada />
            <div className='prodia'>Pronostico Mas de Tres Goles</div>
            <div className='contenedorcajasmasdetres'>
                <Cajamasdetres />
            </div>

            <div className='cuerpomasdetres'></div>
            <Footer />
        </div>
    )
}
export default Masdetres;