import React from 'react';
import { NavLink } from 'react-router-dom'; 
import logo from '../imagenes/logoprincipal.png';
import fotoportada from '../imagenes/fotoportada.JPG';

function Portada() {
    return (
        <div className='portada'>
            <div className='logo'>
                <img src={logo} alt='Logo' />
            </div>
            <div className='fotoportada'>
                <img src={fotoportada} alt='Foto de portada' />
                <div className='textoportada'>
                    <h2 style={{ fontSize: "5vw" }}>
                        Pronósticos Deportivos
                    </h2>
                    <p>
                    Bienvenido a nuestra plataforma, donde la
                     Inteligencia Artificial se une a las apuestas 
                     deportivas. Con una tasa de acierto superior al
                      75% en los mercados de Local, Visita, Mas 2.5 y Ambos Marcan, nuestros métodos son altamente efectivos y
                       rentables. Descubre cómo la tecnología avanzada
                        puede transformar tus 
                    apuestas deportivas y llevarte al éxito.
                    </p>
                </div>
            </div>

            <nav className="navbar navbar-expand-lg">
                <div className="container-fluid">

                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup"
                        aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="collapse navbar-collapse justify-content-center" id="navbarNavAltMarkup" style={{ color: 'white' }}>
                        <div className="navbar-nav">
                            {/* Cambia los <a> por <NavLink> */}
                            <NavLink to="/pronosticosdeldia" className="nav-item nav-link">Pronósticos del Dia</NavLink>
                            <NavLink to="/pronosticosconia" className="nav-item nav-link">Pronosticos empate con IA</NavLink>
                            <NavLink to="/masdetres" className="nav-item nav-link">Mas 3.5 Goles</NavLink>
                            <NavLink to="/historialpronosticos" className="nav-item nav-link">Historial de Pronósticos</NavLink>
                            <NavLink to="/resultadospartidos" className="nav-item nav-link">Resultados Partidos</NavLink>
                        </div>
                    </div>
                </div>
            </nav>
        </div>
    );
}

export default Portada;
