import React, { useEffect } from 'react';
import Portada from '../componentes/portada';
import Footer from '../componentes/footer';

const ResultadosPartidos = () => {

    useEffect(() => {
        const script = document.createElement('script');
        script.src = 'https://www.scorebat.com/embed/embed.js?v=arrv';
        script.async = true;
        script.id = 'scorebat-jssdk';
        document.body.appendChild(script);
        return () => {
            document.body.removeChild(script);
        };
    }, []);

    return (
        <div className='historialpronosticos'>
            <Portada />
            <div className='prodia'>Resultados de Partidos</div>
            <iframe 
                className="fondo"
                src="https://www.scorebat.com/embed/livescore/" 
                frameBorder="0" 
                allowFullScreen 
                allow="autoplay; fullscreen" 
                title="Live Football Scores"

            ></iframe>
            <Footer />  
        </div>
    );
};

export default ResultadosPartidos;
