import React, { useState, useEffect } from 'react';
import axios from 'axios';

import Cajapronosticosempate from '../componentes/cajapronosticosempate';
import Portada from '../componentes/portada';
import Graficos from '../componentes/graficos';
import Tablarendimientoempates from '../componentes/tablarendimientoempates';
import Footer from '../componentes/footer';

function Pronosticosconia() {
    const [stake, setStake] = useState('50');
    const [resultado, setResultado] = useState(null);
    const [diasDiferentes, setDiasDiferentes] = useState(null);
    const [error, setError] = useState('');

    useEffect(() => {
        const handleCalculate = () => {
            axios.get(`http://localhost:3001/calculostake/${stake}`)
                .then(response => {
                    console.log(response.data);
                    setResultado(response.data.total.toLocaleString('es-PE', { style: 'currency', currency: 'PEN' }));
                    setDiasDiferentes(response.data.diasDiferentes);
                    setError('');
                })
                .catch(error => {
                    console.error('Error calculating stake:', error);
                    setResultado(null);
                    setDiasDiferentes(null);
                    setError('Hubo un error al calcular el total. Por favor, inténtalo de nuevo.');
                });
        };

        if (stake !== '') {
            handleCalculate();
        }
    }, [stake]);

    const handleChange = (event) => {
        const inputValue = event.target.value;
        const onlyNumbers = /^\d*$/;

        if (onlyNumbers.test(inputValue)) {
            setStake(inputValue);
        } else {
            setStake('');
        }
    };

    return (
        <div className='pronosticosconia' id='pronosticosconia'>
            <Portada />
            <div className='prodia'>Pronostico Empate con IA</div>
                <div className='howtodo'>"¿Cómo apostar en el mercado de empates? El mercado de empates es nuestra especialidad. Ofrecemos pronósticos diarios basados en IA. Para maximizar tus ganancias, te recomendamos apostar en todos los pronósticos del día. Por ejemplo, si tenemos 7 pronósticos, te sugerimos apostar en los 7, asignando la misma cantidad de dinero a cada uno. Además, te invitamos a revisar nuestro progreso de ganancias diarias, para garantizarte nuestra transparencia. La persistencia te llevará a obtener buenos rendimientos a mediano y largo plazo."</div>

            <a className='linkrendimiento' style={{ textAlign: 'center', fontSize: '20px' }} href='#estadisticasemaptes'>Ver Rendimiento</a>

            <div className='pronosticosconiacontainer'>
                <Cajapronosticosempate />
            </div>
            <div className='estadisticasemaptes' id='estadisticasemaptes'>
                <div className='calculo'>
                    <div className='calculoleft'><h3 style={{ textAlign: 'center', color: 'white', margin: '0' }}>
                        <span className='modi'>Si haces apuestas de S/.
                            <input className='iptbtn'
                                type="text"
                                value={stake}
                                onChange={handleChange}

                            />
                        </span>
                    </h3></div>

                    <div className='calculoright'>
                        {error && <label style={{ color: 'red' }}>{error}</label>}
                        <div className='ganancia'>
                            {resultado !== null && <label>Ganas: {resultado} </label>}

                            {diasDiferentes !== null && <div><label> En  {diasDiferentes} Días</label></div>}

                        </div>
                    </div>


                </div>
                <div className='empates'>
                    <div className='empatesleft'>
                        <h2>Ganacia Acumulada</h2>
                        <Graficos />

                    </div>
                    <div className='empatesright' id='rendimiento'>
                        <h2>Rendimiento Diario</h2>
                        <Tablarendimientoempates />
                    </div>
                </div>
            </div>

            <Footer />
        </div>
    );
}

export default Pronosticosconia;
