import React, { useState, useEffect } from 'react';
import axios from 'axios';
import * as d3 from 'd3';

function Tablarendimiento() {
    const [rendimiento, setRendimiento] = useState([]);
    const [fechaSeleccionada, setFechaSeleccionada] = useState('');
    const [resultadosFecha, setResultadosFecha] = useState([]);
    const [error, setError] = useState('');
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [startIndex, setStartIndex] = useState(0);
    const rowsToShow = 15;

    useEffect(() => {
        axios.get('http://localhost:3001/rendimiento')
            .then(response => {
                setRendimiento(response.data);
            })
            .catch(error => {
                console.error('Error fetching rendimiento:', error);
            });
    }, []);

    const handleRowClick = (date) => {
        const formattedDate = formatDate(date);
        setFechaSeleccionada(formattedDate);
        axios.get(`http://localhost:3001/rendimientoxfecha/${formattedDate}`)
            .then(response => {
                setResultadosFecha(response.data);
                setError('');
                setIsModalOpen(true); // Open the modal
            })
            .catch(error => {
                setError('No hay datos disponibles para la fecha proporcionada. Asegúrate de pasar la fecha en el formato "YYYY-MM-DD".');
                setResultadosFecha([]);
                setIsModalOpen(true); // Open the modal to show the error
            });
    };

    const formatDate = (dateString) => {
        const date = new Date(dateString);
        if (isNaN(date.getTime())) {
            return dateString; // Devolvemos la cadena original si no podemos parsearla como fecha
        }
        const year = date.getUTCFullYear();
        const month = (date.getUTCMonth() + 1).toString().padStart(2, '0');
        const day = date.getUTCDate().toString().padStart(2, '0');
        return `${year}-${month}-${day}`;
    };

    const closeModal = () => {
        setIsModalOpen(false);
    };

    const handleNext = () => {
        setStartIndex(prevIndex => prevIndex + rowsToShow);
    };

    const handlePrev = () => {
        setStartIndex(prevIndex => Math.max(prevIndex - rowsToShow, 0));
    };

    const currentPage = Math.ceil((startIndex + 1) / rowsToShow);
    const totalPages = Math.ceil(rendimiento.length / rowsToShow);

    // Define the color scale
    const colorScale = d3.scaleLinear()
        .domain([0, 50, 100])
        .range(['#F24405', '#FFEC5C', '#03A64A']);

    return (
        <div className="rendimiento-container">
            <div className="leftrendimiento">
                <table className="custom-table">
                    <thead>
                        <tr className="fondocabecera">
                            <th>Fecha</th>
                            <th>N° Pronósticos</th>
                            <th>Aciertos</th>
                            <th style={{ padding: '5px' }}>Porcentaje Aciertos</th>
                        </tr>
                    </thead>
                    <tbody>
                        {rendimiento.slice(startIndex, startIndex + rowsToShow).map((row, index) => (
                            <tr key={index} onClick={() => handleRowClick(row.fecha)} tabIndex="0">
                                <td className='fecharow'>{formatDate(row.fecha)}</td>
                                <td>{row.total_resultados}</td>
                                <td>{row.suma_resultados}</td>
                                <td style={{ backgroundColor: colorScale(row.porcentaje_resultado), fontWeight: 'bold' }}>
                                    {typeof row.porcentaje_resultado === 'number' ? row.porcentaje_resultado.toFixed(2) + '%' : row.porcentaje_resultado} %
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
                <div className='paginationempate'>
                    <button onClick={handlePrev} disabled={startIndex === 0}>Anterior</button>
                    <span>{currentPage} de {totalPages}</span>
                    <button onClick={handleNext} disabled={startIndex + rowsToShow >= rendimiento.length}>Siguiente</button>
                </div>
            </div>

            {isModalOpen && (
                <div id="myModal" className="modal" onClick={closeModal}>
                    <div className="modal-content" onClick={e => e.stopPropagation()}>
                        <div className="rightrendimiento">
                            {error && <p>{error}</p>}
                            {resultadosFecha.length > 0 && (
                                <div>
                                    <h2 className="titulorightrendimiento">Resultados {formatDate(fechaSeleccionada)} </h2>
                                    <table className="custom-table">
                                        <thead > 
                                            <tr className="fondocabecera" >
                                                <th className='modalbreakpoint'>Local</th>
                                                <th className='modalbreakpoint'>Goles Local</th>
                                                <th className='modalbreakpoint'>Goles Visita</th>
                                                <th className='modalbreakpoint'>Visita</th>
                                                <th className='modalbreakpoint'>Pronóstico</th>
                                                <th className='modalbreakpoint'>Resultado</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {resultadosFecha.map((row, index) => (
                                                <tr key={index}>
                                                    <td>{row.local}</td>
                                                    <td>{row.goles_local}</td>
                                                    <td>{row.goles_visita}</td>
                                                    <td>{row.visita}</td>
                                                    <td>{row.pronostico}</td>
                                                    <td className={row.resultado === 1 ? 'resultado-uno' : 'resultado-cero'}>
                                                        {row.resultado === 1 ? 'Ganado' : 'Perdido'}
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}

export default Tablarendimiento;
