import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './estilos.css'; // Asegúrate de importar tu archivo de estilos CSS

function Cajamasdetres() {
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage, setItemsPerPage] = useState(30);
    const [maxPageButtons, setMaxPageButtons] = useState(7);

    // Función para formatear la fecha
    function formatDate(date) {
        const options = { day: '2-digit', month: 'long', year: 'numeric' };
        const formattedDate = new Date(date).toLocaleDateString('es-AR', options);
        const capitalizedDate = formattedDate.replace(/\b\w/g, (char) => char.toUpperCase());
        return capitalizedDate.replace(/ De /g, ' ');
    }

    // Calcular la fecha de hoy en formato simplificado (sin horas)
    const today = new Date();
    today.setHours(0, 0, 0, 0);  // Esto simplifica la fecha a solo año, mes y día

    useEffect(() => {
        axios.get('http://localhost:3001/masdetres')
            .then(response => {
                setData(response.data);
                setLoading(false);
            })
            .catch(error => {
                setError(error);
                setLoading(false);
            });
    }, []);

    const handleSearch = (local, visita, fecha) => {
        const query = `${local} vs ${visita} ${fecha}`;
        const url = `https://www.google.com/search?q=${encodeURIComponent(query)}`;
        window.open(url, '_blank');
    };

    // Paginación
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = data.slice(indexOfFirstItem, indexOfLastItem);

    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth <= 1024) {
                setItemsPerPage(12);
            } 
            if (window.innerWidth <= 768) {
                setItemsPerPage(6);
                setMaxPageButtons(4);
            } else {
                setMaxPageButtons(7);
            }
        };

        handleResize(); // Ejecuta la función para establecer el valor inicial
        window.addEventListener('resize', handleResize); // Añade el event listener

        return () => {
            window.removeEventListener('resize', handleResize); // Limpia el event listener
        };
    }, []);

    // Cambio de página
    const paginate = pageNumber => setCurrentPage(pageNumber);

    if (loading) {
        return <div>Loading...</div>;
    }

    if (error) {
        return <div>Error: {error.message}</div>;
    }

    // Filtrar elementos que tienen la fecha de hoy o futura
    const itemsWithTodayDate = currentItems.filter(item => {
        const itemDate = new Date(item.fecha);
        itemDate.setHours(0, 0, 0, 0);  // Simplificar la fecha a solo año, mes y día
        return itemDate >= today;
    });

    // Calcular los números de página a mostrar
    const totalPages = Math.ceil(data.length / itemsPerPage);
    const pageNumbers = [];
    const halfPageButtons = Math.floor(maxPageButtons / 2);

    let startPage = Math.max(currentPage - halfPageButtons, 1);
    let endPage = Math.min(currentPage + halfPageButtons, totalPages);

    if (currentPage - halfPageButtons < 1) {
        endPage = Math.min(endPage + (halfPageButtons - currentPage + 1), totalPages);
    }
    if (currentPage + halfPageButtons > totalPages) {
        startPage = Math.max(startPage - (currentPage + halfPageButtons - totalPages), 1);
    }

    for (let i = startPage; i <= endPage; i++) {
        pageNumbers.push(i);
    }

    return (
        <div className='container' style={{ width: '100%' }}>
            {itemsWithTodayDate.length === 0 && (
                <div className='no-pronosticos'>
                    Hoy no contamos con pronósticos para este mercado, pero te esperamos el día de mañana.
                </div>
            )}
            <div className='containerpronosticos'>
                {currentItems.map((item, index) => {
                    const itemDate = new Date(item.fecha);
                    itemDate.setHours(0, 0, 0, 0);  // Simplificar la fecha a solo año, mes y día
                    const isTodayOrLater = itemDate >= today;
                    return (
                        <div
                            key={index}
                            className='partidosmasdetres'
                            onClick={() => handleSearch(item.local, item.visita, formatDate(item.fecha))}
                            style={{
                                cursor: 'pointer',
                                border: isTodayOrLater ? '2px solid #0CF25D' : '2px solid #02735E',
                                backgroundColor: isTodayOrLater ? '#008F8C' : 'transparent'
                            }}
                        >
                            <div className='fontdatemasdetre1'>{formatDate(item.fecha)}</div>
                            <div className='fontdatemasdetre2'>{item.local}</div>
                            <div className='fontdatemasdetre2 neon'>VS</div>
                            <div className='fontdatemasdetre2'>{item.visita}</div>
                        </div>
                    );
                })}
            </div>

            <nav className='paginacionemapte' aria-label="Page navigation">
                <ul className="pagination justify-content-center">
                    <li className={`page-item ${currentPage === 1 ? 'disabled' : ''}`}>
                        <button className="page-link" onClick={() => paginate(currentPage - 1)} disabled={currentPage === 1}>Anterior</button>
                    </li>
                    {pageNumbers.map(number => (
                        <li key={number} className={`page-item ${currentPage === number ? 'active' : ''}`}>
                            <button className="page-link" onClick={() => paginate(number)}>{number}</button>
                        </li>
                    ))}
                    <li className={`page-item ${currentPage === totalPages ? 'disabled' : ''}`}>
                        <button className="page-link" onClick={() => paginate(currentPage + 1)} disabled={currentPage === totalPages}>Siguiente</button>
                    </li>
                </ul>
            </nav>
        </div>
    );
}

export default Cajamasdetres;
