import React, { useState, useEffect } from 'react';
import axios from 'axios';

function Cajapronosticos() {
    const [pronosticos, setPronosticos] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage, setItemsPerPage] = useState(10);
    const [pagesToShow, setPagesToShow] = useState(11);

    function formatDate(date) {
        const options = { day: '2-digit', month: 'long', year: 'numeric' };
        const formattedDate = new Date(date).toLocaleDateString('es-ES', options);
        const capitalizedDate = formattedDate.replace(/\b\w/g, (char) => char.toUpperCase());
        return capitalizedDate.replace(/ De /g, ' ');
    }

    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth <= 1024) {
                setItemsPerPage(7);
            } 
            if (window.innerWidth <= 768) {
                setItemsPerPage(5);
            } 
            if (window.innerWidth <= 768) {
                setPagesToShow(5);
            } 
        };

        handleResize();

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    useEffect(() => {
        axios.get('http://localhost:3001/pronosticos')
            .then(response => {
                setPronosticos(response.data);
            })
            .catch(error => {
                console.error('Error fetching pronosticos:', error);
            });
    }, []);

    const paginate = pageNumber => setCurrentPage(pageNumber);

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = pronosticos.slice(indexOfFirstItem, indexOfLastItem);

    const totalPages = Math.ceil(pronosticos.length / itemsPerPage);
    let startPage = Math.max(1, currentPage - Math.floor(pagesToShow / 2));
    let endPage = Math.min(totalPages, startPage + pagesToShow - 1);

    if (totalPages > pagesToShow) {
        const pagesBefore = currentPage - 1;
        const pagesAfter = totalPages - currentPage;
        if (pagesBefore <= Math.floor(pagesToShow / 2)) {
            endPage = pagesToShow;
        } else if (pagesAfter <= Math.floor(pagesToShow / 2)) {
            startPage = totalPages - pagesToShow + 1;
            endPage = totalPages;
        }
    }
    const handleSearch = (local, visita, fecha) => {
        const query = `${local} vs ${visita} ${fecha}`;
        const url = `https://www.google.com/search?q=${encodeURIComponent(query)}`;
        window.open(url, '_blank');
    };
    return (
        <div className='container'>
            <div className='containerpronosticos'>
                {currentItems.map((pronostico, index) => (
                    <div className='cajaresultado' key={index}
                    onClick={() => handleSearch(pronostico.local, pronostico.visita, formatDate(pronostico.fecha))}
                    style={{
                        cursor: 'pointer',

                    }}
                    >
                        <div className='cajafecharesultado'>
                            {formatDate(pronostico.fecha)}
                        </div>
                        <div className='cajacolumna'>
                            <div className={pronostico.resultado === 1 ? 'division verde' : 'division rojo'}></div>
                            <div className='contenidoresultado'>
                                <h4 className='equipo1'>{pronostico.local}</h4>
                                <h4 className='golesresultado'>{pronostico.goles_local}</h4>
                                <h4 className='colorverderesultado'>VS</h4>
                                <h4 className='golesresultado'>{pronostico.goles_visita}</h4>
                                <h4 className='equipo2'>{pronostico.visita}</h4>
                            </div>
                            <div className='separador1'></div>
                            <h4 className='negritaresultado'>Pronosticos: {pronostico.pronostico}</h4>
                        </div>
                    </div>
                ))}
            </div>

            <nav className="paginacionemapte" aria-label="Page navigation">
                <ul className="pagination justify-content-center">
                    <li className={`page-item ${currentPage === 1 ? 'disabled' : ''}`}>
                        <button className="page-link cambio" onClick={() => paginate(currentPage - 1)} disabled={currentPage === 1}>&laquo;</button>
                    </li>
                    {Array.from({ length: (endPage - startPage) + 1 }, (_, i) => (
                        <li key={startPage + i} className={`page-item ${currentPage === startPage + i ? 'active' : ''}`}>
                            <button className="page-link" onClick={() => paginate(startPage + i)}>{startPage + i}</button>
                        </li>
                    ))}
                    <li className={`page-item ${currentPage === totalPages ? 'disabled' : ''}`}>
                        <button className="page-link cambio" onClick={() => paginate(currentPage + 1)} disabled={currentPage === totalPages}>&raquo;</button>
                    </li>
                </ul>
            </nav>
        </div>
    );
}

export default Cajapronosticos;
