import React from 'react';
import Portada from '../componentes/portada';
import Cajapronosticosactual from '../componentes/cajapronosticosactual';
import Footer from '../componentes/footer';

function Pronosticosdeldia() {
    // Obtener la fecha actual
    const fechaActual = new Date().toISOString().split('T')[0];

    // Renderizar solo los pronósticos de la fecha actual
    return (
        <div className='pronosticosdeldia' id='pronosticosdeldia'>
            <Portada />
            <div className='pronosticos_dia'>
                <h1 className='prodia'>Pronosticos del dia</h1>
                <Cajapronosticosactual fecha={fechaActual} />
            </div>
            <Footer />
        </div>
    )
}
export default Pronosticosdeldia;
