import React, { useState, useEffect } from 'react';
import axios from 'axios';

function Cajapronosticosempate() {
    const [pronosticos, setPronosticos] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage, setItemsPerPage] = useState(15); // Estado inicial para itemsPerPage
    const [pagesToShow, setPageToShow] = useState(7); // Estado inicial para itemsPerPage

    function formatDate(date) {
        const options = { day: '2-digit', month: 'long', year: 'numeric' };
        const formattedDate = new Date(date).toLocaleDateString('es-ES', options);
        const capitalizedDate = formattedDate.replace(/\b\w/g, (char) => char.toUpperCase());
        return capitalizedDate.replace(/ De /g, ' ');
    }

    function isToday(date) {
        const today = new Date();
        const comparisonDate = new Date(date);
        return (
            today.getDate() === comparisonDate.getDate() &&
            today.getMonth() === comparisonDate.getMonth() &&
            today.getFullYear() === comparisonDate.getFullYear()
        );
    }

    function isFuture(date) {
        const today = new Date();
        const comparisonDate = new Date(date);
        return comparisonDate > today;
    }

    useEffect(() => {
        axios.get('http://localhost:3001/empates')
            .then(response => {
                setPronosticos(response.data);
            })
            .catch(error => {
                console.error('Error fetching pronosticos:', error);
            });
    }, []);

    // useEffect para cambiar itemsPerPage basado en el tamaño de la ventana
    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth <= 1024) {
                setItemsPerPage(12);
            } 
            if (window.innerWidth <= 768) {
                setItemsPerPage(6);
            } 
        };

        handleResize(); // Ejecuta la función para establecer el valor inicial
        window.addEventListener('resize', handleResize); // Añade el event listener

        return () => {
            window.removeEventListener('resize', handleResize); // Limpia el event listener
        };
    }, []);
    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth <= 768) {
                setPageToShow(5);
            } 
        };

        handleResize(); // Ejecuta la función para establecer el valor inicial
        window.addEventListener('resize', handleResize); // Añade el event listener

        return () => {
            window.removeEventListener('resize', handleResize); // Limpia el event listener
        };
    }, []);
    const paginate = pageNumber => setCurrentPage(pageNumber);

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = pronosticos.slice(indexOfFirstItem, indexOfLastItem);

    // Calcula el rango de páginas a mostrar
    const totalPages = Math.ceil(pronosticos.length / itemsPerPage);
    let startPage = Math.max(1, currentPage - Math.floor(pagesToShow / 2));
    let endPage = Math.min(totalPages, startPage + pagesToShow - 1);

    // Ajusta el inicio y el final si se desplaza demasiado a la izquierda o a la derecha
    if (totalPages > pagesToShow) {
        const pagesBefore = currentPage - 1;
        const pagesAfter = totalPages - currentPage;
        if (pagesBefore <= Math.floor(pagesToShow / 2)) {
            endPage = pagesToShow;
        } else if (pagesAfter <= Math.floor(pagesToShow / 2)) {
            startPage = totalPages - pagesToShow + 1;
            endPage = totalPages;
        }
    }

    return (
        <div className='container'>
            <div className='containerpronosticos'>
                {currentItems.map((pronostico, index) => {
                    const isCurrentOrFuture = isToday(pronostico.fecha) || isFuture(pronostico.fecha);
                    return (
                        <div
                            className='cajapronostico'
                            key={index}
                            style={isCurrentOrFuture ? {
                                border: '2px solid transparent',
                                borderImage: 'linear-gradient(to right bottom, #12eb2e, #75ea0c, #a5e800, #cbe500, #ebe112) 1',
                                borderRadius: '15px'
                            } : {}}
                        >
                            <div className='cajafecha'>
                                {formatDate(pronostico.fecha)}
                            </div>
                            <div className='division'></div>
                            <div className='contenido' style={{ margin: 0 }}>
                                <h4 className='local' style={{ marginTop: 5 }}>{pronostico.local}</h4>
                                <h4 className='colorverde'>VS</h4>
                                <h4 className='visita'>{pronostico.visita}</h4>



                            </div>
                            <div className='contenido2'>
                                <div className='separador'></div>
                                <h5 style={{ margin: '4px 0px 0px 0px', fontSize: '18px', color: '#878787', lineHeight: '0.8' }}>Pronostico</h5>
                                <h4 className='negrita' style={{ marginTop: 0, fontSize: '16px !important', lineHeight: '1' }}>Empate</h4>

                                <h4 className='visita' style={{ marginTop: 10 }}>Cuota</h4>
                                <h4 className='cambiartamaño' style={{ fontFamily: 'Montserrat, sans-serif' }}>{pronostico.cuota}</h4>
                            </div>
                        </div>
                    );
                })}
            </div>

            <nav className='paginacionemapte' aria-label="Page navigation">
                <ul className="pagination justify-content-center">
                    <li className={`page-item ${currentPage === 1 ? 'disabled' : ''}`}>
                        <button className="page-link" onClick={() => paginate(currentPage - 1)} disabled={currentPage === 1}>Anterior</button>
                    </li>
                    {Array.from({ length: (endPage - startPage) + 1 }, (_, i) => (
                        <li key={startPage + i} className={`page-item ${currentPage === startPage + i ? 'active' : ''}`}>
                            <button className="page-link" onClick={() => paginate(startPage + i)}>{startPage + i}</button>
                        </li>
                    ))}
                    <li className={`page-item ${currentPage === totalPages ? 'disabled' : ''}`}>
                        <button className="page-link " onClick={() => paginate(currentPage + 1)} disabled={currentPage === totalPages}>Siguiente</button>
                    </li>
                </ul>
            </nav>
        </div>
    );
}

export default Cajapronosticosempate;
