import React from 'react'
import Cajapronosticos from '../componentes/cajapronosticos';
import Portada from '../componentes/portada';
import Tablarendimiento from '../componentes/tablarendimiento';
import Footer from '../componentes/footer';

function historialpronosticos() {
    return (
        <div className='historialpronosticos' id='historialpronosticos'>
            <Portada />

            <div className='pronosticos_dia'>
                <div className='titulo'>
                    <h1 className='prodia'>Historial de Pronosticos</h1>
                </div>
                <div className='contenidohitorial'>
                    <div className='contenidohitorialleft'>
                        <Cajapronosticos />

                    </div>
                    <div className='contenidohitorialright'>
                        <Tablarendimiento />

                    </div>

                </div>


            </div>
            <Footer />
        </div>
    )
}
export default historialpronosticos
